import React, { useState } from 'react';

const StressSurvey = ({ updateUserData, nextPage, prevPage }) => {
  // Initialize all question responses to null
  const [responses, setResponses] = useState({
    stress1: null, stress2: null, stress3: null, stress4: null, stress5: null,
    stress6: null, stress7: null, stress8: null, stress9: null, stress10: null
  });

  // Questions for the PSS (Perceived Stress Scale) survey
  const questions = [
    { id: 'stress1', text: '            ?', reverse: false },
    { id: 'stress2', text: '            ?', reverse: false },
    { id: 'stress3', text: '          ?', reverse: false },
    { id: 'stress4', text: '            ?', reverse: true },
    { id: 'stress5', text: '             ?', reverse: true },
    { id: 'stress6', text: '            ?', reverse: true },
    { id: 'stress7', text: '           ?', reverse: true },
    { id: 'stress8', text: '           ?', reverse: true },
    { id: 'stress9', text: '                ?', reverse: false },
    { id: 'stress10', text: '               ?', reverse: false }
  ];

  // Options for the survey responses
  const options = [
    { value: 0, text: ' ' },
    { value: 1, text: ' ' },
    { value: 2, text: ' ' },
    { value: 3, text: ' ' },
    { value: 4, text: '  ' }
  ];

  // Handle radio button change
  const handleChange = (questionId, value) => {
    setResponses({
      ...responses,
      [questionId]: parseInt(value)
    });
  };

  // Handle form submission
  const handleSubmit = (e) => {
    e.preventDefault();
    
    // Calculate the stress score with reverse scoring for items 4, 5, 6, 7, 8
    let stressScore = 0;
    questions.forEach(question => {
      const response = responses[question.id];
      if (question.reverse) {
        // Reverse scoring: 0->4, 1->3, 2->2, 3->1, 4->0
        stressScore += (4 - response);
      } else {
        stressScore += response;
      }
    });
    
    // Update user data with the stress score
    updateUserData({ stressScore });
    
    // Move to the next page
    nextPage();
  };

  // Check if all questions have been answered
  const isFormComplete = () => {
    return Object.values(responses).every(value => value !== null);
  };

  return (
    <div className="survey-container">
      <h1> </h1>
      <p>   ,  1        .</p>
      
      <form onSubmit={handleSubmit}>
        <table className="survey-table">
          <thead>
            <tr>
              <th width="50%"></th>
              {options.map((option, index) => (
                <th key={index}>{option.text}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {questions.map((question, qIndex) => (
              <tr key={qIndex}>
                <td>{qIndex + 1}. {question.text}</td>
                {options.map((option, oIndex) => (
                  <td key={oIndex}>
                    <input 
                      type="radio" 
                      name={question.id} 
                      value={option.value}
                      checked={responses[question.id] === option.value}
                      onChange={() => handleChange(question.id, option.value)}
                      required
                    />
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
        
        <div className="button-group">
          <button type="button" className="btn prev-btn" onClick={prevPage}></button>
          <button 
            type="submit" 
            className="btn next-btn"
            disabled={!isFormComplete()}
          >
             
          </button>
        </div>
      </form>
    </div>
  );
};

export default StressSurvey; 