import React, { useState } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import supabase from '../supabaseClient';
import { REGISTRATION_STEPS } from '../config/registrationSteps';
import { validatePhoneNumber, usePhoneNumber } from '../utils/phoneNumberUtils';

const DataCollectionGuide = () => {
  const navigate = useNavigate();
  const location = useLocation();
  
  //   
  const { depressionScore = 0, anxietyScore = 0, stressScore = 0 } = location.state || {};

  //    
  const [personalInfo, setPersonalInfo] = useState({
    name: '',
    phoneNumber: '',
    email: ''
  });

  //    
  const [privacyConsent, setPrivacyConsent] = useState(false);

  //    
  const [emailError, setEmailError] = useState('');
  const [registrationError, setRegistrationError] = useState('');
  
  //   (  )
  const {
    phoneFormatted,
    phoneError,
    handlePhoneChange: handlePhoneInputChange
  } = usePhoneNumber(personalInfo.phoneNumber, (phone) => {
    setPersonalInfo(prev => ({
      ...prev,
      phoneNumber: phone
    }));
    //      
    setRegistrationError('');
    setRegistrationSuccess(false);
  });

  //    
  const [isRegistering, setIsRegistering] = useState(false);
  const [registrationSuccess, setRegistrationSuccess] = useState(false);

  const handleBackClick = () => {
    navigate(-1);
  };

  //   
  const validateEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };
  


  //   
  const handleEmailChange = (e) => {
    const { value } = e.target;
    setPersonalInfo(prev => ({
      ...prev,
      email: value
    }));
    
    if (value && !validateEmail(value)) {
      setEmailError('   .');
    } else {
      setEmailError('');
    }
    
    //      
    setRegistrationError('');
    setRegistrationSuccess(false);
  };
  


  //    ()
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setPersonalInfo(prev => ({
      ...prev,
      [name]: value
    }));
    
    //      
    setRegistrationError('');
    setRegistrationSuccess(false);
  };

  //    
  const handlePrivacyConsentChange = (e) => {
    setPrivacyConsent(e.target.checked);
    
    //       
    setRegistrationError('');
    setRegistrationSuccess(false);
  };

  //   
  const getGroupType = () => {
    if (depressionScore >= 10) {
      return 'depression'; //  
    } else if (stressScore >= 17) {
      return 'stress'; //   
    } else {
      return 'normal'; //  
    }
  };

  //    (DB )
  const handleRegistrationClick = async () => {
    //   
    if (!personalInfo.name.trim() || !personalInfo.phoneNumber.trim() || !personalInfo.email.trim()) {
      setRegistrationError(', ,     .');
      return;
    }

    //   
    if (!privacyConsent) {
      setRegistrationError('     .');
      return;
    }

    //   
    if (!validateEmail(personalInfo.email)) {
      setRegistrationError('     .');
      return;
    }

    //   
    if (!validatePhoneNumber(personalInfo.phoneNumber)) {
      setRegistrationError(' 010  11  .');
      return;
    }

    //     DB   
    setIsRegistering(true);
    setRegistrationError('');
    setRegistrationSuccess(false);

    try {
      //    
      const { data: recruitmentData, error: recruitmentError } = await supabase
        .from('recruitment_status')
        .select('is_recruiting')
        .order('updated_at', { ascending: false })
        .limit(1);

      if (recruitmentError) {
        console.error('Recruitment status check error:', recruitmentError);
        setRegistrationError('    .     .');
        return;
      }

      //   
      if (recruitmentData && recruitmentData.length > 0 && !recruitmentData[0].is_recruiting) {
        setRegistrationError('   .      .');
        return;
      }
      //   
      const waitlistData = {
        name: personalInfo.name.trim(),
        phone: personalInfo.phoneNumber,
        email: personalInfo.email.trim(),
        depressive: depressionScore,
        anxiety: anxietyScore,
        stress: stressScore,
        registration_step: REGISTRATION_STEPS.SURVEY_COMPLETED, //  
        //   3( )   
        // experiment_consent, data_usage_consent, third_party_consent  DB (NULL) 
      };

      // survey-person   insert
      const { data, error } = await supabase
        .from('survey-person')
        .insert([waitlistData]);

      if (error) {
        console.error('Registration error:', error);
        setRegistrationError('   .     .');
        return;
      }

      // RPC   
      if (data && data.length > 0) {
        const result = data[0];
        if (!result.success) {
          if (result.message.includes('already registered')) {
            setRegistrationError('    .');
          } else {
            setRegistrationError(result.message || '   .');
          }
          return;
        }
      }

      //    
      setRegistrationSuccess(true);
      
      // 3    
      setTimeout(() => {
        navigate('/');
      }, 3000);

    } catch (error) {
      setRegistrationError('  .     .');
    } finally {
      setIsRegistering(false);
    }
  };

  return (
    <div className="data-collection-guide">
      <div className="guide-header">
        <h1>      </h1>
      </div>
      
      <div className="guide-content">
        <div className="guide-intro">
          <p>
                       3         . 
          </p>
        </div>

        <div className="guide-section">
          <h2>  </h2>
          <ul>
            <li> 1  </li>
            <li>    (      )</li>
            <li>       </li>
          </ul>
        </div>

        <div className="guide-section">
          <h2>  </h2>
          <ul>
            <li> : <strong>25.6.30 ()  </strong></li>
            <li> : <strong> 50,   25,  25</strong></li>
            <li> : <strong>25.7.14  25.9.28 (    3)</strong></li>
          </ul>
        </div>

        <div className="guide-section">
          <h2>    </h2>
          <ol>
            <li>     <strong>' '  ,  </strong> .</li>
            <li> <strong> </strong>      <strong>  </strong> (  
                    .)</li>
            <li>  ,  ,  ,  ,    <strong></strong>  .</li>
            <li>   , <strong> </strong> .</li>
            <li>   <strong> (KIST  )   </strong>. (  
                   )</li>
            <li><strong>    </strong>  . (     )</li>
            <li>   <strong>2         .</strong> 
             , <strong>   ,      </strong>  .</li>
            <li> <strong>   6</strong>  ,     .</li>
            <li>    8:00 - 20:00,   20:00 - 8:00   .</li>
            <li> 1(20:00) <strong>1    </strong>  .</li>
            <li>   <strong>  </strong>.</li>
            <li>     <strong> 1   </strong>  (1 ).</li>
            <li>       ()  .</li>
          </ol>
        </div>

        <div className="guide-section">
          <h2>  </h2>
          <p>       (100,000) .</p>
        </div>

        <div className="guide-section">
          <h2>  </h2>
          <p>         
            (<a href="https://open.kakao.com/o/gTH3TfCg" target="_blank" rel="noopener noreferrer">
                https://open.kakao.com/o/gTH3TfCg
            </a>, <strong>'2025'</strong> )</p>
        </div>

        {/*    -   */}
        <div className="guide-section">
          <h2>  </h2>
          <p> <strong>
            {getGroupType() === 'depression' ? '' : 
            getGroupType() === 'stress' ? ' ' : ''}</strong>     .</p>
          <p>           .</p>

          {/*      */}
          <div className="registration-form-container">
            <h4>    </h4>
            
            <div className="registration-form">
              <div className="personal-info-section">
                <h5> </h5>
                
                <div className="form-group">
                  <label htmlFor="name"></label>
                  <input
                    type="text"
                    id="name"
                    name="name"
                    value={personalInfo.name}
                    onChange={handleInputChange}
                    className="form-input"
                    required
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="email"></label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    value={personalInfo.email}
                    onChange={handleEmailChange}
                    className={emailError ? 'form-input input-error' : 'form-input'}
                    required
                  />
                  {emailError && <p className="error-message">{emailError}</p>}
                </div>

                <div className="form-group">
                  <label htmlFor="phoneNumber"></label>
                  <input
                    type="tel"
                    id="phoneNumber"
                    name="phoneNumber"
                    value={phoneFormatted}
                    onChange={handlePhoneInputChange}
                    placeholder="010-0000-0000"
                    className={phoneError ? 'form-input input-error' : 'form-input'}
                    required
                  />
                  {phoneError ? (
                    <p className="error-message">{phoneError}</p>
                  ) : (
                    <small className="helper-text">: 010-0000-0000</small>
                  )}
                </div>
              </div>

              <div className="consent-section">
                <div className="consent-checkbox">
                  <input
                    type="checkbox"
                    id="privacyConsent"
                    checked={privacyConsent}
                    onChange={handlePrivacyConsentChange}
                    className="consent-checkbox-input"
                  />
                  <label htmlFor="privacyConsent" className="consent-checkbox-label">
                    []    .
                  </label>
                </div>

                <div className="privacy-notice-box">
                  <ul>
                    <li>   : , ,  ,  ,  </li>
                    <li> :           </li>
                    <li>:   </li>
                  </ul>
                </div>
              </div>

              <div className="registration-button-container">
                {registrationError && (
                  <p className="error-message">{registrationError}</p>
                )}
                {registrationSuccess && (
                  <div className="success-container">
                    <p className="success-message">
                        .  .<br/>
                      <small style={{color: '#666'}}>3    .</small>
                    </p>
                    <button 
                      type="button" 
                      className="btn home-btn"
                      onClick={() => navigate('/')}
                      style={{ marginTop: '10px' }}
                    >
                       
                    </button>
                  </div>
                )}
                {!registrationSuccess && (
                  <button 
                    type="button" 
                    className={`btn register-btn ${isRegistering ? 'registering' : ''}`}
                    onClick={handleRegistrationClick}
                    disabled={isRegistering}
                  >
                    {isRegistering ? ' ...' : '   '}
                  </button>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
      
      <div className="guide-actions">
        <button 
          className="btn back-btn"
          onClick={handleBackClick}
        >
            
        </button>
      </div>
    </div>
  );
};

export default DataCollectionGuide; 